 .wptravel-block-trip-filters{
    .wp-travel-itinerary-items{
        #datepicker1,
        #datepicker2 {
            padding: 0.6em 2em;
        }
    }
    .wp-trave-price-range.wp-travel-form-field {
        input#amount {
        color: var(--wp--preset--color--primary) !important;
        }
    } 
    .wp-trave-price-range .ui-slider-horizontal {
        .ui-slider-range {
            top: 0;
            height: 100%;
            background: var(--wp--preset--color--primary);
        }

        .ui-slider-handle {
            margin-left: -0.35em;
        }
    }
    .wp-travel-itinerary-items .wp-travel-form-field {
        margin-bottom: 10px;
        label {
            display: block;
            position: relative;
            width: 100%;
            font-size: 16px;
            font-weight: 500;
            color: #4e4e4e;
        }
        select, input{
            width: 100%;
            padding: 0.6em 0.6em;
        }
        .trip-duration-calender .calender-icon {
            position: absolute;
            top: -41px;
            left: 3px;
            width: 28px;
            background: none;
        }
    }
    .wp-travel-form-field.wp-travel-trip-duration{
        position: relative;
    }
 }

 .wptravel-block-trip-filters span.trip-duration-calender{
    position:  relative !important;
 }

.wptravel-block-trip-filters .calender-icon::before {
    top: 7px;
    right: -5px;
    position: relative;
}

.wptravel-block-trip-filters .calender-icon:before {
    content: "ÔÅ≥";
    font-family: "Font Awesome 5 Free";
}

.wptravel-block-trip-filters .wp-travel-itinerary-items .wp-travel-search #wp-travel-filter-search-submit {
    cursor: pointer;
}